.contact {
    padding: 80px 100px 100px;
    display: flex;
    justify-content: center;
  }
  
  .contact__column {
    width: 50%;
  }
  
  .contact__title {
    font-size: 3.0625rem;
    line-height: 3.6875rem;
  }
  
  .contact__instruction {
    margin: 22px 0 42px;
    font-size: 1rem;
    line-height: 1.375rem;
  }

  .contact__details {
    display: flex;
    justify-content: flex-start;
    color: var(--color-grey);
    align-items: center;
  }
  
  .details__item {
    color: var(--color-grey);
    font-weight: 500;
    text-decoration: none;
  }
  
  .details__item:first-of-type {
    margin-right: 20px;
  }
  
  .details__item img {
    margin-right: 10px;
  }

  .form {
    display: flex;
    flex-direction: column;
  }
  
  .form__field {
    display: flex;
    flex-direction: column;
  }
  
  .form__field:first-of-type {
    margin-bottom: 42px;
  }
  
  .form__label {
    font-weight: bold;
    text-transform: uppercase;
    color: var(--color-primary);
    line-height: 0.9rem;
    letter-spacing: 3px;
    font-size: 0.6875rem;
  }
  
  .form__input {
    border: none;
    border-bottom: 1px solid var(--color-grey);
    background-color: transparent;
    line-height: 2.625rem;
  }

  .form__consent {
    margin-top: 30px;
  }
  
  .form__checkbox {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 1.375rem;
  }
  
  .form__checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .form__checkbox .checkbox {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: var(--color-white);
    border: 1px solid var(--color-grey);
    border-radius: 4px;
  }
  
  .form__checkbox input:checked ~ .checkbox {
    background-image: url(../assets/Checkmark.svg);
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
  }

  .form__checkbox .consent__text {
    margin: 0 10px;
    font-size: 0.75rem;
    color: var(--color-grey);
    line-height: 17px;
  }
  
  .form .btn {
    font-size: 0.8125rem;
    padding: 20px 97px;
    align-self: flex-end;
    margin-top: 20px;
  }

.contact {
    padding: 80px 100px 100px;
    display: flex;
    justify-content: center;
  
    @include tablet {
      flex-direction: column;
      align-items: center;
      padding: 50px 15px;
    }
  
    &__column {
      width: 50%;
  
      @include  tablet {
        width: 100%;
      }
    }
  
    &__title {
      font-size: 3.0625rem;
      line-height: 3.6875rem;
    }
  
    &__instruction {
      margin: 22px 0 42px;
      font-size: 1rem;
      line-height: 1.375rem;
  
      @include tablet {
        margin: 5px 0 35px;
      }
    }
  
    &__details {
      display: flex;
      justify-content: flex-start;
      color: $color-grey;
      align-items: center;
  
      @include tablet {
        flex-direction: column;
        align-items: flex-start;
      }
  
      .details__item {
        color: $color-grey;
        font-weight: 500;
        text-decoration: none;
  
        @include tablet {
          margin-bottom: 10px;
        }
  
        &:first-of-type {
          margin-right: 20px;
        }
  
        img {
          margin-right: 10px;
        }
      }
    }
  }